import { api } from '@/utils/api'
import { storage } from '@/utils/storage'
import router from '@/router'

const state = {
  signedIn: false,
  accessToken: null,
  loginErr: false,
  curLocation: null
}

const mutations = {
  setSignIn (state, bool) {
    state.signedIn = bool
  },
  setToken (state, val) {
    state.accessToken = val
  },
  setLoginBad (state, bool) {
    state.loginErr = bool
  },
  setLocation (state, val) {
    state.curLocation = val
  }
}

const actions = {
  loginUser ({commit}, user) {
    api.rawPost('/v1/admin/login', user)
    .then( response => {
      let token = response.data.token
      storage.storeValue('token', token)
      commit('setToken', token)
      commit('setSignIn', true)
      commit('setLoginBad', false)
      return true
    })
    .catch(error => {
      console.log(error)
      commit('setSignIn', false)
      commit('setLoginBad', true)
      return false
    })
  },
  isValid ({commit}) {
    console.log('called')
    api.get('/v1/admin/test')
      .then(response => {
        commit('setSignIn', true)
        commit('setLoginBad', false)
        console.log(response)
      })
      .catch(error => {
        commit('setSignIn', false)
        console.log(error)
      })
  },
  signOut ({commit}) {
    router.push('/')
    storage.clearValue('token')
    commit('setSignIn', false)
  },
  moveTo  ({commit}, target) {
    console.log(target)
    router.push({name: target})
    commit('setLocation', target)
  }
}

const getters = {
  signedIn: state => state.signedIn,
  accessToken: state => {
    console.log(storage.getValue('token'))
    api.get('/v1/admin/test')
      .then(response => {
        console.log(response)
      })
      .catch(error => {
        console.log(error)
      })
    state.accessToken
  },
  loginErr: state => state.loginErr
}

export default {
  state,
  actions,
  mutations,
  getters
}
