<template>
  <div class="container">
    <div class="signin">
      <h1 class="centered">login</h1>
      <hr style="width:80%;" />
      <div style="margin: 32px 0px;">
        <p><input type="text" placeholder="USER ID" v-model="email" /></p>
        <p><input type="password" placeholder="PASSWORD" v-model="password" /></p>
        <p><button @click="login">Log In</button></p>
      </div>
      <hr style="width:80%;" />
      <div class="sub" >
        <sub>Using the login information provided to you, sign in to begin administrative operations</sub>
        <p><br/></p>
        <p v-if="loginErr" style="coloe:#F00;">
          Incorrect Login information
        </p>
        <p><br/></p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'signin-view',
  data () {
    return {
      email: null,
      password: null
    }
  },
  methods: {
    login () {
      this.loginUser({ email: this.email, password: this.password})
    },
    ...mapActions([
      'loginUser'
    ])
  },
  computed: {
    ...mapGetters([
      'loginErr'
    ])
  }
}
</script>

<style>
.signin {
  width: 50%;
  margin:0px auto;
  background-color: #fff;
